.services {
  @include newSection();

  .service {
    margin: 30px 0;
    .service-icon {
      font-size: 28px;
    }
    .paragraph-title {
      font-size: $big-mobile-font-size;
      margin-bottom: 15px;
    }
    p {
      font-size: $small-mobile-font-size;
    }
  }
  .show-more-services {
    @include goldButton();
    margin-top: 30px;
    font-size: $small-mobile-font-size;
    border: none;
    ion-icon {
      font-size: 15px;
      margin-right: 10px;
    }
  }
}

@media (min-width: 1439px) {
  .services {
    .service {
      .paragraph-title {
        font-size: 2em;
      }
      p {
        font-size: 1.5em;
        padding: 0 70px;
      }
    }
  }
}

@media (min-width: 1919px) {
  .services {
    .service {
      .paragraph-title {
        font-size: 3em;
      }
      p {
        font-size: 1.7em;
      }
    }
  }
}
